import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpModule } from '@angular/http';
import { FormsModule } from '@angular/forms';

// Routes
import { appRouting, appRoutingProviders } from './app.routes';

// Services
import { AuthService } from './services/auth.service';
import { UserService } from './services/user.service';
import { ProductService } from './services/product.service';
import { CategoryService } from './services/category.service';
import { SubcategoryService } from './services/subcategory.service';

// Components
import { AppComponent } from './app.component';
import { LoginComponent } from './components/login/login.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { PageTitleComponent } from './components/dashboard/pageTitle/pageTitle.component';
import { ProductComponent } from './components/dashboard/product/product.component';
import { ProductAddComponent } from './components/dashboard/product/product-add.component';
import { ProductEditComponent } from './components/dashboard/product/product-edit.component';
import { CategoryComponent } from './components/dashboard/category/category.component';
import { CategoryAddComponent } from './components/dashboard/category/category-add.component';
import { CategoryEditComponent } from './components/dashboard/category/category-edit.component';
import { SubCategoryComponent } from './components/dashboard/subcategory/subcategory.component';
import { SubCategoryAddComponent } from './components/dashboard/subcategory/subcategory-add.component';
import { SubCategoryEditComponent } from './components/dashboard/subcategory/subcategory-edit.component';
import { ErrorComponent } from './components/error/error.component';

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    DashboardComponent,
    PageTitleComponent,
    ProductComponent,
    ProductAddComponent,
    ProductEditComponent,
    CategoryComponent,
    CategoryAddComponent,
    CategoryEditComponent,
    SubCategoryAddComponent,
    SubCategoryEditComponent,
    SubCategoryComponent,
    ErrorComponent
  ],
  imports: [
    BrowserModule,
    HttpModule,
    FormsModule,
    appRouting
  ],
  providers: [
    appRoutingProviders,
    AuthService,
    ProductService,
    UserService,
    CategoryService,
    SubcategoryService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



