import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { Http, RequestOptions, Headers } from '@angular/http';
import 'rxjs/add/operator/map';

import { Global } from '../services/global';

@Injectable()
export class UserService {
    public url: string;

    constructor(
        private _router: Router,
        private _http: Http
    ) {
        this.url = Global.url;
    }

    getUser() {
        let user = JSON.parse(localStorage.getItem('user'));
        user = user[0];
        return user;
    }

    setUser(user: any) {
        localStorage.setItem('user', JSON.stringify(user));
    }

    getUsers() {
        return this._http.get(this.url + 'user')
            .map(res => res.json());
    }

    addUser(user: any) {
        const json = JSON.stringify(user);
        const headers = new Headers({ 'Content-Type': 'application/json' });

        return this._http.post(this.url + 'user', json, { headers: headers })
            .map(res => res.json());
    }

    updateUser(id: number, user: any) {
        const json = JSON.stringify(user);
        const headers = new Headers({ 'Content-Type': 'application/json' });

        return this._http.put(this.url + 'user/' + id, json, { headers: headers })
            .map(
                res => {
                    return res.json();
                }
            );
    }

    saveStorage(res) {
        const message = res.message;
        if (message === 'correct login') {
            localStorage.setItem('user', JSON.stringify(res.user));
            this._router.navigate(['/dashboard']);
        } else {
            return false;
        }
    }

    authenticate(user_name: string, user_password: string) {
        const user = {
            user_name: user_name,
            user_password: user_password
        };
        const json = JSON.stringify(user);
        const headers = new Headers({ 'Content-Type': 'application/json' });

        return this._http.post(this.url + 'login', json, { headers: headers })
            .map(res => res.json());
    }

    verifyUser(): boolean {
        if (localStorage.getItem('user')) {
            return true;
        }
        return false;
    }
}



