import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ProductComponent } from './product/product.component';
import { ProductAddComponent } from './product/product-add.component';
import { ProductEditComponent } from './product/product-edit.component';
import { CategoryComponent } from './category/category.component';
import { CategoryAddComponent } from './category/category-add.component';
import { CategoryEditComponent } from './category/category-edit.component';
import { SubCategoryComponent } from './subcategory/subcategory.component';
import { SubCategoryAddComponent } from './subcategory/subcategory-add.component';
import { SubCategoryEditComponent } from './subcategory/subcategory-edit.component';
import { ErrorComponent } from '../../components/error/error.component';

export const dashboardRoutes: Routes = [
    { path: 'product', component: ProductComponent },
    { path: 'product-add', component: ProductAddComponent },
    { path: 'product-edit/:id', component: ProductEditComponent },
    { path: 'category', component: CategoryComponent },
    { path: 'category-add', component: CategoryAddComponent },
    { path: 'category-edit/:id', component: CategoryEditComponent },
    { path: 'subcategory', component: SubCategoryComponent },
    { path: 'subcategory-add', component: SubCategoryAddComponent },
    { path: 'subcategory-edit/:id', component: SubCategoryEditComponent },
    { path: '', pathMatch: 'full', redirectTo: 'product' },
    { path: '**', component: ErrorComponent }
];



