import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { Http, RequestOptions, Headers } from '@angular/http';
import 'rxjs/add/operator/map';

import { Global } from './global';

@Injectable()
export class SubcategoryService {
    public url: string;

    constructor(
        private _router: Router,
        private _http: Http
    ) {
        this.url = Global.url;
    }

    getSubcategories() {
        return this._http.get(this.url + 'subcategory')
            .map(res => res.json());
    }

    getSubcategory(id: number) {
        return this._http.get(this.url + 'subcategory/' + id)
            .map(res => res.json());
    }

    addSubcategory(subcategory: any) {
        const json = JSON.stringify(subcategory);
        const headers = new Headers({ 'Content-Type': 'application/json' });

        return this._http.post(this.url + 'subcategory', json, { headers: headers })
            .map(res => res.json());
    }

    updateSubcategory(id: number, subcategory: any) {
        const json = JSON.stringify(subcategory);
        const headers = new Headers({ 'Content-Type': 'application/json' });

        return this._http.put(this.url + 'subcategory/' + id, json, { headers: headers })
            .map(
                res => {
                    return res.json();
                }
            );
    }

    deleteSubcategory(id: number) {
        const headers = new Headers({ 'Content-Type': 'application/json' });

        return this._http.delete(this.url + 'subcategory/' + id, { headers: headers })
            .map(
                res => {
                    return res.json();
                }
            );
    }
}



