import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { CategoryService } from '../../../services/category.service';
import { SubcategoryService } from '../../../services/subcategory.service';

@Component({
    selector: 'app-addcategory',
    templateUrl: './subcategory-form.component.html'
})
export class SubCategoryAddComponent implements OnInit {
    public title: string;
    public subcategory: {
        category_id: 0,
        name: '',
        description: '',
        image: ''
    };
    public categories: any[];

    constructor(
        private _router: Router,
        private _categoryService: CategoryService,
        private _subcategoryService: SubcategoryService,
    ) {
        this.title = 'Add Subcategorie';
        this.subcategory = {
            category_id: 0,
            name: '',
            description: '',
            image: ''
        };
        this.categories = [];
    }

    ngOnInit() {
        this.getCategories();
    }

    getCategories() {
        this._categoryService.getCategories()
            .subscribe(
                res => {
                    if (res.message === 'Bad Request') {
                        console.log(res);
                    } else {
                        this.categories = res.category;
                        // console.log(this.categories);
                    }
                },
                err => {
                    console.log(<any>err);
                }
            );
    }

    onSubmit() {
        this._subcategoryService.addSubcategory(this.subcategory)
            .subscribe(
                res => {
                    if (res.message !== 'SubCategory Added') {
                        console.log(res);
                    } else {
                        this._router.navigate(['/dashboard/category']);
                    }
                },
                err => {
                    console.log(<any>err);
                }
            );
    }

}



