import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { CategoryService } from '../../../services/category.service';
import { SubcategoryService } from '../../../services/subcategory.service';

@Component({
    selector: 'app-editcategory',
    templateUrl: './subcategory-form.component.html'
})
export class SubCategoryEditComponent implements OnInit {
    public title: string;
    public subcategoryID: number;
    public subcategory: {
        name: '',
        description: '',
        image: '',
        category_id: 0,
        subcategory_id: 0
    };
    public categories: any[];

    constructor(
        private _router: Router,
        private _activatedRoute: ActivatedRoute,
        private _categoryService: CategoryService,
        private _subcategoryService: SubcategoryService,
    ) {
        this.title = 'Edit Subcategorie';
        this.subcategory = {
            name: '',
            description: '',
            image: '',
            category_id: 0,
            subcategory_id: 0
        };
        this.categories = [];
    }

    ngOnInit() {
        this._activatedRoute.params.subscribe(params => { this.subcategoryID = params['id']; });
        this.getCategories();
        this.getSubCategory();
    }

    getCategories() {
        this._categoryService.getCategories()
            .subscribe(
                res => {
                    if (res.message === 'Bad Request') {
                        console.log(res);
                    } else {
                        this.categories = res.category;
                        // console.log(this.categories);
                    }
                },
                err => {
                    console.log(<any>err);
                }
            );
    }

    getSubCategory() {
        this._subcategoryService.getSubcategory(this.subcategoryID)
            .subscribe(
                res => {
                    if (res.message !== 'Return SubCategory') {
                        console.log(res);
                    } else {
                        this.subcategory = res.subcategory;
                        // console.log(this.category);
                    }
                },
                err => {
                    console.log(<any>err);
                }
            );
    }

    onSubmit() {
        this._subcategoryService.updateSubcategory(this.subcategoryID, this.subcategory)
            .subscribe(
                res => {
                    if (res.message !== 'SubCategory Updated') {
                        console.log(res);
                    } else {
                        this._router.navigate(['/dashboard/subcategory']);
                    }
                },
                err => {
                    console.log(<any>err);
                }
            );
    }

}



