import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { CategoryService } from '../../../services/category.service';

@Component({
    selector: 'app-editcategory',
    templateUrl: './category-form.component.html'
})
export class CategoryEditComponent implements OnInit {
    public title: string;
    public categoryID: number;
    public category: {
        name: '',
        description: '',
        image: '',
        state: true
    };

    constructor(
        private _router: Router,
        private _activatedRoute: ActivatedRoute,
        private _categoryService: CategoryService,
    ) {
        this.title = 'Edit Categorie';
        this.category = {
            name: '',
            description: '',
            image: '',
            state: true
        };
    }

    ngOnInit() {
        this._activatedRoute.params.subscribe(params => { this.categoryID = params['id']; });
        this.getCategory();
    }

    getCategory() {
        this._categoryService.getCategory(this.categoryID)
            .subscribe(
                res => {
                    if (res.message !== 'Return Category') {
                        console.log(res);
                    } else {
                        this.category = res.category;
                        // console.log(this.category);
                    }
                },
                err => {
                    console.log(<any>err);
                }
            );
    }

    onSubmit() {
        this._categoryService.updateCategory(this.categoryID, this.category)
            .subscribe(
                res => {
                    if (res.message !== 'Category Updated') {
                        console.log(res);
                    } else {
                        this._router.navigate(['/dashboard/category']);
                    }
                },
                err => {
                    console.log(<any>err);
                }
            );
    }

}



