import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { ProductService } from '../../../services/product.service';
import { SubcategoryService } from '../../../services/subcategory.service';

@Component({
    selector: 'app-editcategory',
    templateUrl: './product-form.component.html'
})
export class ProductEditComponent implements OnInit {
    public title: string;
    public productID: number;
    public product: {
        name: '',
        description: '',
        image: '',
        price: 0,
        quantity: 0,
        state: true,
        subcategory_id: 0
    };
    public subcategories: any[];

    constructor(
        private _router: Router,
        private _activatedRoute: ActivatedRoute,
        private _productService: ProductService,
        private _subcategoryService: SubcategoryService,
    ) {
        this.title = 'Edit Subcategorie';
        this.product = {
            name: '',
            description: '',
            image: '',
            price: 0,
            quantity: 0,
            state: true,
            subcategory_id: 0
        };
        this.subcategories = [];
    }

    ngOnInit() {
        this._activatedRoute.params.subscribe(params => { this.productID = params['id']; });
        this.getSubcategories();
        this.getProduct();
    }

    getSubcategories() {
        this._subcategoryService.getSubcategories()
            .subscribe(
                res => {
                    if (res.message === 'Bad Request') {
                        console.log(res);
                    } else {
                        this.subcategories = res.subcategory;
                        // console.log(this.subcategories);
                    }
                },
                err => {
                    console.log(<any>err);
                }
            );
    }

    getProduct() {
        this._productService.getProduct(this.productID)
            .subscribe(
                res => {
                    if (res.message !== 'Return Product') {
                        console.log(res);
                    } else {
                        this.product = res.product;
                        // console.log(this.product);
                    }
                },
                err => {
                    console.log(<any>err);
                }
            );
    }

    onSubmit() {
        console.log(this.product);
        this._productService.updateProduct(this.productID, this.product)
            .subscribe(
                res => {
                    if (res.message !== 'Product Updated') {
                        console.log(res);
                    } else {
                        this._router.navigate(['/dashboard/product']);
                    }
                },
                err => {
                    console.log(<any>err);
                }
            );
    }
}



